"use strict";
(() => {
  // src/utils/skeletonUtil.ts
  var cloneElementWithUniqueIds = (element, prefix = "__clone") => {
    const clonedElement = element.cloneNode(true);
    function updateIds(el, parentPrefix) {
      if (el.id) {
        el.id = `${parentPrefix}-${el.id}`;
      }
      Array.from(el.children).forEach((child, index) => {
        updateIds(child, `${parentPrefix}-${index}`);
      });
    }
    updateIds(clonedElement, prefix);
    return clonedElement;
  };
  var removeElement = (ele) => {
    const parent = ele.parentNode;
    if (parent) {
      parent.removeChild(ele);
    }
  };
  var inViewPort = (ele) => {
    try {
      const rect = ele.getBoundingClientRect();
      return rect.top < window.innerHeight && rect.left < window.innerWidth;
    } catch (e) {
      return true;
    }
  };
  var hasAttr = (ele, attr) => {
    try {
      return ele.hasAttribute(attr);
    } catch (e) {
      return false;
    }
  };
  var setOpacity = (ele) => {
    if (ele.style) {
      ele.style.opacity = 0;
    }
  };
  var px2rem = (px) => {
    const pxValue = typeof px === "string" ? parseInt(px, 10) : px;
    const htmlElementFontSize = window.getComputedStyle(
      document.documentElement
    ).fontSize;
    return `${pxValue / parseInt(htmlElementFontSize, 10)}rem`;
  };
  var emptyHandler = (node) => {
    node.innerHTML = "";
    let classNameArr = node.className && node.className.split(" ") || [];
    classNameArr = classNameArr.map((item) => {
      return "." + item;
    });
    const className = classNameArr.join("");
    const id = node.id ? "#" + node.id : "";
    const query = className || id;
    if (!query) return;
    let styleSheet;
    for (const item of document.styleSheets) {
      if (!item.href) {
        styleSheet = item;
        break;
      }
    }
    try {
      styleSheet && styleSheet.insertRule(
        `${query}::before{content:'' !important;background:none !important;}`,
        0
      );
      styleSheet && styleSheet.insertRule(
        `${query}::after{content:'' !important;background:none !important;}`,
        0
      );
    } catch (e) {
      console.log("handleEmptyNode Error: ", JSON.stringify(e));
    }
  };
  var checkHasPseudoEle = (ele) => {
    if (!ele) return false;
    const beforeComputedStyle = window.getComputedStyle(ele, "::before");
    const beforeContent = beforeComputedStyle.getPropertyValue("content");
    const beforeWidth = parseFloat(beforeComputedStyle.getPropertyValue("width")) || 0;
    const hasBefore = beforeContent && beforeContent !== "none";
    const afterComputedStyle = window.getComputedStyle(ele, "::after");
    const afterContent = afterComputedStyle.getPropertyValue("content");
    const afterWidth = parseFloat(afterComputedStyle.getPropertyValue("width")) || 0;
    const hasAfter = afterContent && afterContent !== "none";
    const width = Math.max(beforeWidth, afterWidth);
    if (hasBefore || hasAfter) {
      return { hasBefore, hasAfter, ele, width };
    }
    return false;
  };
  var isTextNode = (node) => {
    return node.nodeType === Node.TEXT_NODE && node.nodeValue !== "";
  };
  var isHtmlElement = (node) => {
    return node.nodeType === Node.ELEMENT_NODE;
  };

  // src/skeleton.ts
  var SKELETON_CLONE_CLASS = "skeleton_clone";
  var MOCK_WRAPPER_ID = "skeleton-text-wrapper-id";
  var MAIN_COLOR = "#EEEEEE";
  var MAIN_COLOR_RGB = "rgb(238, 238, 238)";
  var PSEUDO_CLASS = "sk-pseudo";
  var PSEUDO_CLASS_ANIMATION = "sk-pseudo-animation";
  var BUTTON_CLASS = "sk-button";
  var TRANSPARENT_CLASS = "sk-transparent";
  var SMALLEST_BASE64 = "data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7";
  var IMG_BASE64_LARGE = "data:image/image/png;base64,";
  var IMG_BASE64_MID = "data:image/image/png;base64,";
  var IMG_BASE64_SMALL = "data:image/image/png;base64,";
  var IMG_BG = "#dddddd";
  var SKELETON_TEXT_CLASS = "skeleton-text-block-mark";
  var IMG_CLASS_SMALL = "sk-img-small";
  var IMG_CLASS_MID = "sk-img-mid";
  var IMG_CLASS_LARGE = "sk-img-large";
  var skeletonStyle = `

      .${SKELETON_CLONE_CLASS}{
        position:fixed;
        left:0;
        right:0;
        top:0;
        bottom:0;
        z-index:1000000;
        width:100vw;
        height:100vh;
      }

      .${SKELETON_TEXT_CLASS},
      .${SKELETON_TEXT_CLASS} * {
        background-origin: content-box;
        background-clip: content-box;
        background-color: transparent;
        color: transparent !important;
        background-repeat: repeat-y;
      }
  
      .${PSEUDO_CLASS}::before,
      .${PSEUDO_CLASS}::after {
        background: ${MAIN_COLOR} !important;
        background-image: none !important;
        color: transparent !important;
        border-color: transparent !important;
      }
      .${PSEUDO_CLASS_ANIMATION}::before,
      .${PSEUDO_CLASS_ANIMATION}::after {
          animation: skeleton-opacity-animation 1.5s ease infinite !important;
      }
  
      .${BUTTON_CLASS} {
        box-shadow: none !important;
      }

      .${IMG_CLASS_SMALL}{
         background-image: url(${IMG_BASE64_SMALL}) !important;
         background-repeat:no-repeat !important;
         background-position: center !important;
      }
      .${IMG_CLASS_MID}{
         background-image: url(${IMG_BASE64_MID}) !important;
         background-repeat:no-repeat !important;
         background-position: center !important;
      }
      .${IMG_CLASS_LARGE}{
         background-image: url(${IMG_BASE64_LARGE}) !important;
         background-repeat:no-repeat !important;
         background-position: center !important;
      }
  
      .${TRANSPARENT_CLASS}::before,
      .${TRANSPARENT_CLASS}::after {
        opacity: 0 !important;
      }
      
      @keyframes skeleton-opacity-animation {
        0% {
          opacity: 1;
        }
        30% {
          opacity: 0.65;
        }
        100% {
         opacity: 1;
        }
      }
  
    `;
  var SkeletonRender = class {
    constructor(options) {
      this.options = Object.assign(
        {
          debug: false,
          animation: true,
          appId: "app",
          minGrayBlockWidth: 30,
          minGrayPseudoWidth: 30
        },
        options
      );
    }
    removeMock() {
      const getTextWidthElement = document.querySelector(`#${MOCK_WRAPPER_ID}`);
      if (getTextWidthElement) getTextWidthElement.remove();
    }
    async genSkeleton() {
      await this.startGenSkeleton();
      this.removeMock();
    }
    async startGenSkeleton() {
      debugger;
      const app = document.getElementById(this.options.appId);
      if (!app) {
        console.error("\u6839\u5143\u7D20\u6CA1\u627E\u5230~");
        return false;
      }
      const newNode = cloneElementWithUniqueIds(app);
      newNode.id = "__skeleton_clone";
      newNode.classList.add(SKELETON_CLONE_CLASS);
      app.style.visibility = "hidden";
      document.body.prepend(newNode);
      this.styleHandler();
      try {
        this.handleNode(newNode);
      } catch (e) {
        if (e instanceof Error) {
          console.log("==genSkeleton Error==\n", e.message, e.stack);
        }
      }
    }
    // Initialization processing DOM
    styleHandler() {
      const skeletonBlockStyleEle = document.createElement("style");
      skeletonBlockStyleEle.innerText = skeletonStyle.replace(/\n/g, "");
      document.body.prepend(skeletonBlockStyleEle);
    }
    // Processing a single node
    handleNode(node) {
      if (!node) return;
      if (isTextNode(node)) {
        const parent = node.parentNode;
        if (!parent.classList.contains(SKELETON_TEXT_CLASS)) {
          const textContent = node.textContent.replace(/[\r\n]/g, "").trim();
          if (textContent) {
            const tmpNode = document.createElement("i");
            tmpNode.classList.add(SKELETON_TEXT_CLASS);
            tmpNode.innerHTML = textContent;
            node.parentNode.replaceChild(tmpNode, node);
            this.textHandler(tmpNode);
            return true;
          }
        }
      } else if (isHtmlElement(node)) {
        if (!inViewPort(node) || hasAttr(node, "data-skeleton-remove")) {
          return removeElement(node);
        }
        const ignore = hasAttr(node, "data-skeleton-ignore") || node.tagName === "STYLE";
        if (ignore) return;
        this.beforeHandler(node);
        this.pseudoHandler(node);
        const tagName = node.tagName && node.tagName.toUpperCase();
        switch (tagName) {
          case "SCRIPT":
            this.scriptHandler(node);
            break;
          case "IMG":
            this.imgHandler(node);
            break;
          case "SVG":
            this.svgHandler(node);
            break;
          // 处理input 元素
          // 删除placeholder 和 value
          case "INPUT":
            this.inputHandler(node);
            break;
          // 处理button
          // 1. 设置boxshadow为none
          // 2. 设置元素渲染出的宽高，清空子元素
          case "BUTTON":
            this.buttonHandler(node);
            break;
          // 处理A标签
          // 1. 删除href属性
          case "A":
            this.aHandler(node);
            break;
        }
        if (node.childNodes && node.childNodes.length === 1 && node.childNodes[0].nodeType === Node.TEXT_NODE) {
          this.textHandler(node);
          return;
        }
        if (node.hasChildNodes()) {
          node.childNodes.forEach((node2) => this.handleNode(node2));
        }
      }
    }
    // 处理文本
    // 1. 获取行高
    // 2. 根据元素高度计算渲染几行
    // 3. 利用background-repeat 和background-size y等于一行的高度 渲染出多行的骨架屏
    // 4. 如果是多行，则处理最后一行的对齐方式，新增一个元素，利用定位覆盖
    textHandler(ele) {
      const comStyle = window.getComputedStyle(ele);
      let {
        display,
        lineHeight,
        paddingTop,
        paddingRight,
        paddingBottom,
        paddingLeft,
        position: pos,
        fontSize,
        textAlign,
        wordSpacing,
        wordBreak
      } = comStyle;
      if (!/\d/.test(lineHeight)) {
        const fontSizeNum = parseInt(fontSize, 10) || 14;
        lineHeight = `${fontSizeNum * 1.4}px`;
      }
      const position = ["fixed", "absolute", "flex"].find((p) => p === pos) ? pos : "relative";
      const height = ele.offsetHeight;
      const width = ele.offsetWidth;
      let lineCount = (height - parseFloat(paddingTop) - parseFloat(paddingBottom)) / parseFloat(lineHeight) || 0;
      lineCount = lineCount < 1.5 ? 1 : lineCount;
      const textHeightRatio = 0.6;
      ele.classList.add(SKELETON_TEXT_CLASS);
      Object.assign(ele.style, {
        backgroundImage: `linear-gradient(
            transparent ${(1 - textHeightRatio) / 2 * 100}%,
            ${MAIN_COLOR} 0%,
            ${MAIN_COLOR} ${((1 - textHeightRatio) / 2 + textHeightRatio) * 100}%,
            transparent 0%
          )`,
        backgroundSize: `100% ${px2rem(parseInt(lineHeight) * 1.1)}`,
        position
      });
      debugger;
      if (display === "inline") {
        return;
      }
      if (lineCount > 1) {
        this.addTextMask(
          ele,
          Object.assign(JSON.parse(JSON.stringify(comStyle)), {
            lineHeight
          })
        );
      } else {
        const textWidth = this.getTextWidth(ele, {
          fontSize,
          lineHeight,
          wordBreak,
          wordSpacing
        });
        const textWidthPercent = textWidth / (width - parseInt(paddingRight, 10) - parseInt(paddingLeft, 10));
        ele.style.backgroundSize = `${textWidthPercent * 100}% ${px2rem(parseInt(lineHeight) * 1.1)}`;
        switch (textAlign) {
          case "left":
            break;
          case "right":
            ele.style.backgroundPositionX = "100%";
            break;
          case "center":
            ele.style.backgroundPositionX = "50%";
        }
      }
    }
    addTextMask(paragraph, {
      textAlign,
      lineHeight,
      paddingBottom,
      paddingLeft,
      paddingRight
    }, maskWidthPercent = 0.5) {
      debugger;
      let left;
      let right;
      switch (textAlign) {
        case "center":
          left = document.createElement("span");
          right = document.createElement("span");
          [left, right].forEach((mask) => {
            Object.assign(mask.style, {
              display: "inline-block",
              width: `${maskWidthPercent / 2 * 100}%`,
              height: lineHeight,
              background: "#fff",
              position: "absolute",
              bottom: paddingBottom
            });
          });
          left.style.left = paddingLeft;
          right.style.right = paddingRight;
          paragraph.appendChild(left);
          paragraph.appendChild(right);
          break;
        case "right":
          left = document.createElement("span");
          Object.assign(left.style, {
            display: "inline-block",
            width: `${maskWidthPercent * 100}%`,
            height: lineHeight,
            background: "#fff",
            position: "absolute",
            bottom: paddingBottom,
            left: paddingLeft
          });
          paragraph.appendChild(left);
          break;
        case "left":
        default:
          right = document.createElement("span");
          Object.assign(right.style, {
            display: "inline-block",
            width: `${maskWidthPercent * 100}%`,
            height: lineHeight,
            background: "#fff",
            position: "absolute",
            bottom: paddingBottom,
            right: paddingRight
          });
          paragraph.appendChild(right);
          break;
      }
    }
    getTextWidth(ele, style) {
      const MOCK_TEXT_ID = "skeleton-text-id";
      let offScreenParagraph = document.querySelector(`#${MOCK_TEXT_ID}`);
      if (!offScreenParagraph) {
        const wrapper = document.createElement("p");
        wrapper.id = MOCK_WRAPPER_ID;
        offScreenParagraph = document.createElement("span");
        Object.assign(wrapper.style, {
          width: "10000px",
          position: "absolute",
          top: "0"
        });
        offScreenParagraph.id = MOCK_TEXT_ID;
        offScreenParagraph.style.visibility = "hidden";
        wrapper.appendChild(offScreenParagraph);
        document.body.appendChild(wrapper);
      }
      Object.assign(offScreenParagraph.style, style);
      offScreenParagraph.innerHTML = ele.innerHTML;
      return offScreenParagraph.getBoundingClientRect().width;
    }
    scriptHandler(node) {
      removeElement(node);
    }
    imgHandler(node) {
      if (node.src) {
        if (/^data:image/.test(node.src)) {
          return false;
        } else if (node.src.indexOf(
          "https://zeekrlife-oss.oss-cn-hangzhou.aliyuncs.com/frontend"
        ) !== -1 || node.src.indexOf("https://zeekrlife-oss.zeekrlife.com/frontend") !== -1) {
          return false;
        }
      }
      const { width, height } = node.getBoundingClientRect();
      node.style.width = width + "px";
      node.style.height = height + "px";
      node.src = SMALLEST_BASE64;
      node.style.backgroundColor = IMG_BG;
      if (width >= 300 || height >= 300) {
        node.classList.add(IMG_CLASS_LARGE);
      } else if (width >= 150 || height >= 150) {
        node.classList.add(IMG_CLASS_MID);
      } else if (width >= 75 || height >= 75) {
        node.classList.add(IMG_CLASS_SMALL);
      }
      this.addAnimationForElement(node);
    }
    aHandler(node) {
      node.removeAttribute("href");
    }
    svgHandler(node) {
      const { width, height } = node.getBoundingClientRect();
      if (width === 0 || height === 0 || node.getAttribute("aria-hidden") === "true") {
        return removeElement(node);
      }
      node.innerHTML = "";
      Object.assign(node.style, {
        width: px2rem(parseInt(width + "")),
        height: px2rem(parseInt(height + ""))
      });
      this.addAnimationForElement(node);
    }
    inputHandler(node) {
      node.removeAttribute("placeholder");
      node.value = "";
    }
    buttonHandler(node) {
      if (!node.tagName) return;
      node.classList.add(BUTTON_CLASS);
      let { width, height } = window.getComputedStyle(node);
      node.style.width = width;
      node.style.height = height;
      node.innerHTML = "";
      this.addAnimationForElement(node);
    }
    /**
     * Processing text nodes
     * @param {*} node Node
     * @return {Boolean} True means that processing has been completed, false means that processing still needs to be continued
     */
    pseudoHandler(node) {
      if (!node.tagName) return;
      const pseudo = checkHasPseudoEle(node);
      if (!pseudo || !pseudo.ele) return;
      const { ele, width } = pseudo;
      if (width < this.options.minGrayPseudoWidth || 30) {
        return ele.classList.add(TRANSPARENT_CLASS);
      }
      ele.classList.add(PSEUDO_CLASS);
      if (this.options.animation) {
        ele.classList.add(PSEUDO_CLASS_ANIMATION);
      }
    }
    beforeHandler(node) {
      if (!node.tagName) return;
      if (hasAttr(node, "data-skeleton-empty")) {
        emptyHandler(node);
      }
      const { width } = node.getBoundingClientRect();
      const isHidden = width < this.options.minGrayBlockWidth;
      if (isHidden) {
        setOpacity(node);
      }
      const ComputedStyle = window.getComputedStyle(node);
      if (ComputedStyle.backgroundImage !== "none") {
        node.style.backgroundImage = "none";
        node.style.background = MAIN_COLOR;
        !isHidden && this.addAnimationForElement(node);
      }
      if (ComputedStyle.boxShadow !== "none") {
        const oldBoxShadow = ComputedStyle.boxShadow;
        const newBoxShadow = oldBoxShadow.replace(/^rgb.*\)/, MAIN_COLOR_RGB);
        node.style.boxShadow = newBoxShadow;
      }
      if (ComputedStyle.borderColor) {
        node.style.borderColor = MAIN_COLOR;
      }
      const bgColor = node.getAttribute("data-skeleton-bgcolor");
      if (bgColor) {
        node.style.backgroundColor = bgColor;
        node.style.color = "transparent";
        !isHidden && this.addAnimationForElement(node);
      }
    }
    addAnimationForElement(ele) {
      if (ele) {
        ele.style.animation = this.options.animation ? "skeleton-opacity-animation 1.5s ease infinite" : "";
      }
    }
  };
  window.SkeletonRender = SkeletonRender;
})();
